@import url('https://fonts.googleapis.com/css2?family=Cairo&display=swap');

*{
  font-family: 'Cairo', sans-serif;
  margin: 0px;
  padding: 0px;
}

#navbar{
  display: flex;
  justify-content: space-between;
  padding: 8px;
  text-align: center;
  align-items: center;
  background-color: #2D3748;
  color: white;
}

#name{
  padding-left: 20px;
}

#container{
  display: flex;
}

#headers{
  display: flex;
}
#headers>h2{
  padding-right: 20px;
}
.link{
  text-decoration: none;
  color: white;
}

#img{
  height: 40px;
  padding-right: 20px;
  display: none;
}


@media screen and (max-width: 900px) {
  #img{
    display: block;
    width: 40px;
  }
  #navbar>div{
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    padding: 5px;
  }
  #headers,#navbar{
    flex-direction: column;
  }
  #headers{
    height: 0px;
    opacity: 0;
    transition: all 0.5s ;
  }
  #headers.active{
    height: 300px;
    opacity: 1;
  }


}
